import React, { useState, useRef, useEffect } from "react";
import { TextInput, View, FlatList, Text, Image, TouchableOpacity, StyleSheet, Platform, ImageSourcePropType, Dimensions } from "react-native";
import Modal from "react-native-modal";
import GoToHomeButton from "../componentes/GoToHomeButton";
import { games } from "../data/games";
import DetailScreen from "./DetailScreen";

export default function SearchGameScreen({ isVisible, onGoToHomeButtonPress }: { isVisible: boolean, onGoToHomeButtonPress: () => void }) {
    const [query, setQuery] = useState("");
    const [selectedGame, setSelectedGame] = useState<any | null>(null);
    const [inputFocused, setInputFocused] = useState(false);
    // Cuando el modal se abre, reinicia el focus y placeholder
    useEffect(() => {
        if (isVisible) {
            setInputFocused(false);
        }
    }, [isVisible]);
    const inputRef = useRef<TextInput>(null);

    const handleGoToHomeButtonPress = () => {
        setQuery("");
        setSelectedGame(null);
        onGoToHomeButtonPress();
    };

    // Filtrar juegos por nombre
    const filteredGames = query.trim().length === 0
        ? []
        : games.filter(g => g.name.toLowerCase().includes(query.trim().toLowerCase()));

    const isLaptop = Platform.OS === 'web' && screenWidth > 900;

    return (
        <Modal
            isVisible={isVisible}
            animationIn="fadeIn"
            animationOut="fadeOut"
            animationInTiming={900}
            animationOutTiming={900}
            backdropOpacity={isLaptop ? 0 : 0.4}
            style={modalStyles.modalArea}
            onBackdropPress={!isLaptop ? handleGoToHomeButtonPress : undefined}
        >
            <View style={modalStyles.modalContent}>
                <View style={{ width: '100%', alignItems: 'center', position: 'relative' }}>
                    <View style={modalStyles.searchRow}>
                        <View style={{ flexDirection: 'row', alignItems: 'center', width: '100%', justifyContent: 'center' }}>
                            <View style={{ flexDirection: 'row', alignItems: 'center', width: isLaptop ? '90%' : '100%' }}>
                                <TouchableOpacity
                                    activeOpacity={1}
                                    style={{ flex: 1 }}
                                    onPress={() => {
                                        setInputFocused(true);
                                        setTimeout(() => inputRef.current?.focus(), 10);
                                    }}
                                >
                                    <TextInput
                                        ref={inputRef}
                                        style={modalStyles.input}
                                        placeholder={inputFocused ? '' : 'Buscar juego...'}
                                        value={query}
                                        onChangeText={setQuery}
                                        onFocus={() => setInputFocused(true)}
                                        onBlur={() => setInputFocused(false)}
                                        selectionColor="#333"
                                        textAlign="center"
                                        showSoftInputOnFocus={true}
                                        autoFocus={false}
                                        textAlignVertical="center"
                                        caretHidden={false}
                                    />
                                </TouchableOpacity>
                                {isLaptop && (
                                    <TouchableOpacity onPress={handleGoToHomeButtonPress} style={modalStyles.goHomeButton}>
                                        <GoToHomeButton />
                                    </TouchableOpacity>
                                )}
                            </View>
                        </View>
                    </View>
                    {filteredGames.length > 0 && (
                        <View style={modalStyles.suggestionsContainer}>
                            <FlatList
                                data={filteredGames}
                                keyExtractor={item => item.id.toString()}
                                style={modalStyles.suggestions}
                                keyboardShouldPersistTaps="handled"
                                renderItem={({ item }) => (
                                    <TouchableOpacity
                                        style={modalStyles.suggestionItem}
                                        onPress={() => {
                                            setSelectedGame(item);
                                            setQuery(""); // Limpiar búsqueda al abrir detalle
                                        }}
                                    >
                                        <Image source={item.mainImage as ImageSourcePropType} style={modalStyles.thumbnail} />
                                        <Text style={modalStyles.suggestionText}>{item.name}</Text>
                                    </TouchableOpacity>
                                )}
                            />
                        </View>
                    )}
                </View>
            </View>
            {/* Modal de detalle del juego */}
            <DetailScreen
                isVisible={!!selectedGame}
                game={selectedGame}
                onGoToHomeButtonPress={handleGoToHomeButtonPress}
                onClose={() => setSelectedGame(null)}
            />
        </Modal>
    );
}

